.popup__title {
    margin: 34px auto 0 36px;
    padding: 0;
    font-family: 'Inter', Arial, Helvetica, sans-serif;
    font-style: normal;
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
    color: #000;
}

@media screen and (max-width: 560px) {
    .popup__title {
        font-size: 18px;
        line-height: 22px;
        margin: 15px 22px 10px 22px;
    }
}
